import { Component, ViewChild } from '@angular/core';
import { NavController, AlertController } from "ionic-angular";
import { AuthService } from "../../providers/auth-service";
import { Http } from '@angular/http';
import { HttpHeaders } from '@angular/common/http';
import { Headers } from '@angular/http';
import chartJs from 'chart.js';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

    //INICIO VARIABLES GRAFICOS
    @ViewChild('barCanvas') barCanvas;
    @ViewChild('lineCanvas') lineCanvas;

    barChart: any;
    lineChart: any;
    //FIN VARIABLES GRAFICOS

  results: Array<any> = [];
  statics: Array<any> = [];
  years: Array<any> = [];

  constructor(
    private alertCtrl: AlertController,
    public navCtrl: NavController,
    public http: Http,
    public authService: AuthService
  ) {}
  
  
  ionViewDidLoad(){
  	this.getAsignaturas();
    this.getYears();

    //for(var i = 0; i < this.results.length; i++){
      //console.log(this.getEstadist(this.results.code[i])); 
    //}
  }
  
  getAsignaturas() {
  	let url = 'https://api.sebastian.cl/academia/api/v1/courses/subjects';
  
    let headers = new Headers();
	  headers.append('X-API-KEY', this.authService.apiKey);

	  return this.http.get(url, { headers: headers })
        .subscribe((data) => {
          //lleva datos objeto a un array ordenado
          this.results = JSON.parse(data['_body']);
        },
        err => {
          console.log("ERROR!: ", err);
        });
  }

  getYears() {
    let url = 'https://api.sebastian.cl/academia/api/v1/rankings/years';
  
    let headers = new Headers();
    headers.append('X-API-KEY', this.authService.apiKey);

    return this.http.get(url, { headers: headers })
        .subscribe((data) => {
          //lleva datos objeto a un array ordenado
          this.years = JSON.parse(data['_body']);
          console.log(this.years);
        },
        err => {
          console.log("ERROR!: ", err);
        });
  }

  getEstadist(code:string){
    let url = 'https://api.sebastian.cl/academia/api/v1/rankings/years/';
  
    let headers = new Headers();
    headers.append('X-API-KEY', this.authService.apiKey);

    return this.http.get(url+code, { headers: headers })
        .subscribe((data) => {
          //lleva datos objeto a un array ordenado
          this.statics = JSON.parse(data['_body']);
          console.log(this.statics);
        },
        err => {
          console.log("ERROR!: ", err);
        });

  }


  //=====================INI CODIGO GRAFICOS===========================

  ngAfterViewInit(){
    setTimeout(() => {
      this.barChart = this.getBarChart();
      this.lineChart = this.getLineChart();
    }, 150)
  }

  getChart(context, chartType, data, options?) {
    return new chartJs(context, {
      data,
      options,
      type: chartType
    })
  }


  getBarChart(){
    const data = {
      labels: ['Vermelho', 'Azul', 'Amarelo', 'Verde', 'Roxo'],
      datasets: [{
        label: 'número de votos',
        data: [12, 23, 15, 90, 5],
        backgroundColor: [
          'rgb(255, 0, 0)',
          'rgb(20, 0, 255)',
          'rgb(255, 230, 0)',
          'rgb(0, 255, 10)',
          'rgb(60, 0, 70)'
        ],
        borderWidth: 1
      }]
    };

    const options = {
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true
          }
        }]
      }
    }

    return this.getChart(this.barCanvas.nativeElement, 'bar', data, options);
  }

  getLineChart(){
    const data = {
      labels: ['Janeiro', 'Fevereiro', 'Março', 'Abril'],
      datasets: [{
        label: 'Meu Dataset',
        fill: false,
        lineTension: 0.1,
        backgroundColor: 'rgb(0, 178, 255)',
        borderColor: 'rgb(231, 205, 35)',
        borderCapStyle: 'butt',
        borderJoinStyle: 'miter',
        pointRadius: 1,
        pointHitRadius: 10,
        data:[20, 15, 98, 4],
        scanGaps: false,
      }, {
        label: 'Meu segundo Dataset',
        fill: false,
        lineTension: 0.1,
        backgroundColor: 'rgb(117, 0, 49)',
        borderColor: 'rgb(51, 50, 46)',
        borderCapStyle: 'butt',
        borderJoinStyle: 'miter',
        pointRadius: 1,
        pointHitRadius: 10,
        data:[29, 135, 13, 70],
        scanGaps: false,
      }
    ]
    }

    return this.getChart(this.lineCanvas.nativeElement, 'line', data)
  }


  //=====================FIN CODIGO GRAFICOS===========================

}



